@import url(https://fonts.googleapis.com/css2?family=Cabin&display=swap);
:root {
  --green: #007f68;
  --light-green: #2cb978;
  --dark-green: #146666;
  --light-orange: #f7931e;
  --orange: #f15a24;
  --dark-blue: #172d51;
  --teal: #00A99D;
  --white: #fff;
  --black: #242222;
}

* {
  margin: 0;
  padding: 0;
  font-family: "Cabin", sans-serif;
}

li {
  list-style-type: none;
}

a {
  text-decoration: none;
}

h1 {
  letter-spacing: 2px;
}

header {
  position: relative;
  height: 100vh;
  width: 100%;
  text-align: center;
  overflow: hidden;
}

.computer-img {
  height: 70%;
  opacity: 0;
  transform: scale(0);
}

header .banner {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.nav-wrap {
  width: 100%;
  letter-spacing: 2.5px; 
  z-index: 100;
  position: fixed;
  transition: 0.5s ease-out;
}

.nav-wrap.active {
  background: var(--green);
  transition: 0.5s ease-out;
}

.nav-wrap.active .nav-link {
  color: var(--white);
}

.nav-wrap.active .nav-link:hover {
  color: var(--light-orange);
}

.nav-menu {
  min-height: 3rem;
  width: auto;
  text-align: center;
}

.nav-menu li {
  position: relative;
  list-style: none;
  height: 3rem;
  display: inline-block;
}

.nav-link {
  display: inline-block;
  font-size: 1rem;
  padding: 8px 13px;
  line-height: 32px;
  cursor: pointer;
  color: var(--green);
  transition: 0.2s;
}

.nav-link:hover {
  color: var(--light-orange);
  transition: 0.2s;
}

.menu-icon {
  display: none;
}

.icon1 {
  color: var(--light-orange);
  font-size: 3rem;
  position: absolute;
  top: 20%;
  right: 30%;
  transform: scale(0);
  opacity: 0;
}

.icon2 {
  display: flex;
  color: var(--teal);
  font-size: 3rem;
  position: absolute;
  top: 75%;
  right: 65%;
  transform: scale(0);
  opacity: 0;
}

.icon3 {
  color: var(--orange);;
  font-size: 3rem;
  position: absolute;
  top: 18%;
  right: 52%;
  transform: scale(0);
  opacity: 0;
}

@media screen and (max-width: 800px) {
  header {
    height: 50vh;
  }

  header .banner {
    height: 50vh;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100vh;
    padding-top: 3rem;
    padding-bottom: 3rem;
    position: absolute;
    top: 0;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: var(--black);
    left: 0;
    opacity: 0.98;
    transition: all 0.5s ease;
    z-index: 1;
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
    z-index: 100;
  }

  .menu-icon.dark svg {
    color: var(--green);
    transition: 0.5s ease;
  }
  .menu-icon.light svg {
    color: var(--white);
    transition: 0.5s ease;
  }

  .nav-link {
    color: var(--white);
  }

  .icon1, .icon2, .icon3 {
    font-size: 2rem;
  }

  .icon1 {
    right: 25%;
  }
}

#about {
  padding: 5rem 2rem;
  background-color: var(--dark-green);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1rem;
}

.picture {
  position: relative;
  width: 120px;
  height: 120px;
  padding: 0 3rem;
  opacity: 0;
}

.about-text {
  color: var(--white);
  opacity: 0;
}


@media only screen and (max-width: 800px) {
  .about-text p {
    font-weight: 100;
  }

  .picture {
    padding: 0 1rem;
  }
}
.projects {
  height: 85vh;
  padding: 4rem;
  background: var(--light-green);
}

.projects h1 {
  text-align: center;
  color: var(--white);
}

.cards-container {
  display: flex;
  flex-flow: column;
  align-items: center;
  max-width: 1120px;
  width: 90%;
  margin: 3rem auto auto auto;
}

.cards-wrapper {
  position: relative;
  margin: 50px 0 45px;
}

.cards-items {
  margin-bottom: 24px;
  opacity: 0;
}

.cards-item {
  display: flex;
  flex: 1 1;
  margin: 0 1rem;
  background-color: var(--white);
  border-radius: 20px;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.3);
}

.cards-item-link {
  display: flex;
  flex-flow: column;
  width: 100%;
  border-radius: 20px;
  overflow: hidden;
  text-decoration: none;
}

.cards-item-pic-wrap {
  position: relative;
  width: 100%;
  padding-top: 67%;
  overflow: hidden;
}

.cards-item-pic-wrap::after {
  content: attr(data-category);
  position: absolute;
  bottom: 0;
  margin-left: 10px;
  padding: 6px 8px;
  max-width: calc((100%) - 60px);
  font-size: 12px;
  font-weight: 700;
  color: var(--white);
  background-color: var(--light-orange);
  box-sizing: border-box;
  letter-spacing: 1px;
}

.cards-item-img {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  max-width: 100%;
  height: 100%;
  max-height: 100%;
  object-fit: cover;
  transition: all 0.2s linear;
}

.cards-item-img:hover {
  transform: scale(1.1);
}

.cards-item-info {
  padding: 20px 20px 20px;
}

.cards-item-text {
  color: var(--green);
  font-size: 18px;
  line-height: 24px;
  height: 3rem;
}

.project-links {
  margin-top: 1rem;
  text-align: center;
}

.project-links a {
  color: var(--light-green);
  font-size: 26px;
  margin: 1rem;
  transition: 0.2s;
}

.project-links a:hover {
  color: var(--green);
  transition: 0.2s;
}

@media only screen and (min-width: 1024px) {
  .cards-items {
    display: flex;
  }
}

@media only screen and (max-width: 1024px) {
  .cards-item {
    margin-bottom: 2rem;
  }
  .projects {
    height: 100%;
    padding: 4rem 2rem 2rem 2rem;
  }

  .cards-container {
    display: flex;
    flex-flow: column;
    align-items: center;
    width: 90%;
    margin: 1rem auto auto auto;
  }
}

.skills {
  background-color: var(--white);
  padding: 4rem;
  color: var(--dark-blue);
}

.skills h1 {
  text-align: center;
  color: var(--dark-blue);
}

.skills-container {
  padding: 4rem 6rem 0 4rem;
  max-width: 1120px;
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-template-rows: 1fr 1fr 1fr 1fr;
  grid-template-areas:
    "titre1 css"
    "titre1 js"
    "titre2 php"
    "titre2 php";
  justify-content: center;
  align-items: center;
  text-align: center;
}

.h3-frontend {
  grid-area: titre1;
}

.h3-backend {
  grid-area: titre2;
}

.css {
  grid-area: css;
}

.js {
  grid-area: js;
}

.backend {
  grid-area: php;
}

.skills-container ul {
  display: grid;
  grid-template-columns: repeat(3, 150px);
}

.skills-container li {
  display: flex;
  align-items: center;
}

.skills-container li svg {
  font-size: 40px;
  padding: 1rem;
}

@media screen and (max-width: 850px) {
  .skills {
    padding: 2rem 2rem 2rem 2rem;
  }
  .skills-container {
    padding: 4rem 1rem 0 1rem;
    max-width: 1120px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr;
    grid-template-areas:
      "titre1 css"
      "titre1 js"
      "titre2 php"
      "titre2 php";
  }

  .skills-container ul {
    display: flex;
    flex-wrap: wrap;
  }

  .skills-container li svg {
    font-size: 20px;
  }
}

footer {
  background-color: var(--green);
  padding-top: 4rem;
  padding-bottom: 4rem;
  color: var(--white);
  font-size: 0.75rem;
  text-align: center;
  position: relative;
}

.footer-networks {
  display: flex;
  justify-content: center;
}

.footer-networks li {
  margin: 0 5px;
}

.footer-networks li svg {
  font-size: 32px;
  color: var(--white);
  transform: scale(1.0);
  transition: 0.3s ease-out;
}

.footer-networks li svg:hover {
  transform: scale(1.2);
  transition: 0.3s ease-out;
}

footer p {
    margin-top: 1rem;
}
